.content {
  width: 1200px;
  margin: auto;
}
.startup {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #ffffff7a;
  backdrop-filter: blur(3px);
  animation-name: startup;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  opacity: 1;
  visibility: visible;
  animation-delay: 3s;
}

@keyframes startup {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}

.startupLogo {
  animation-name: startuplogo-fade;
  animation-timing-function: ease-in-out;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  opacity: 0;
  animation-delay: 0;
}

.startupLogo span {
  width: 400px;
  height: 200px;
  display: block;
  background: url("../images/logo.jpg");
  background-position: center;
}

@keyframes startuplogo-fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.startupText {
  margin-top: 50px;
}

.startupText span {
  font-size: 50px;
  font-family: "Ubuntu";
  font-weight: 200;
  animation-name: startuptext-fade;
  animation-timing-function: ease-in-out;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  opacity: 0;
  margin-left: 0px;
  animation-delay: 1.5s;
}

@keyframes startuptext-fade {
  from {
    opacity: 0;
    margin-left: 0px;
  }
  to {
    opacity: 1;
    margin-left: 0px;
  }
}

