.list {
}

.list li {
  width: 96%;
  border: 1px solid #eee;
  border-radius: 5px;
  margin: 2%;
  display: flex;
  font-size: 14px;
  font-family: "OpenSans";
}

.listitem {
  padding: 10px;
  box-sizing: border-box;
}

.listitem-imp {
  width: 20%;
}
.listitem-cit {
  width: 20%;
}
.listitem-nath {
  width: 20%;
}
.listitem-order {
  width: 15%;
}
.listitem-type {
  width: 19%;
}
.listitem-tools {
  width: 5%;
  text-align: center;
}

.listitem-header {
  font-weight: 500;
}

.listitem input {
  width: 80%;
  font-size: 14px;
  font-family: "OpenSans";
}

.listitem select {
  width: 100%;
  font-size: 14px;
  font-family: "OpenSans";
}

.delete-item {
  font-size: 24px;
  margin-top: -5px;
  margin-bottom: -10px;
  font-weight: 700;
  display: inline-flex;
  width: 44px;
  justify-content: center;
  background: #f443360d;
  color: #f00;
  border-radius: 5px;
  cursor: pointer;
}

.add-item {
  width: 96%;
  padding: 2%;
  text-align: right;
}

.add-btn {
  width: 100px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #4caf5021;
  border-radius: 10px;
  font-size: 30px;
  font-weight: bold;
  color: #4caf50;
  margin-right: 0;
  margin-left: auto;
  cursor: pointer;
}

.calc {
  width: max-content;
  background: #00bcd4;
  padding: 10px;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  font-family: Ubuntu;
  font-weight: 300;
  margin-bottom: 30px;
}

.result {
